<template>
  <div class="container">
    <h2>購物車列表</h2>
    <table class="table align-middle">
      <thead>
        <tr>
          <th>圖片</th>
          <th>商品名稱</th>
          <th>價格</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td style="width: 200px">
            <div
              :style="{ backgroundImage: `url(${item.imageUrl})` }"
              style="
                height: 100px;
                background-size: cover;
                background-position: center;
              "
            ></div>
          </td>
          <td>
            {{ item.title }}
          </td>
          <td>
            <div class="h5" v-if="item.price === item.origin_price">
              {{ item.price }} 元
            </div>
            <div v-else>
              <del class="h6">原價 {{ item.origin_price }} 元</del>
              <div class="h5">現在只要 {{ item.price }} 元</div>
            </div>
          </td>
          <td>
            <div class="btn-group btn-group-sm">
              <button
                type="button"
                class="btn btn-outline-secondary"
                @click="openProductModal(item.id)"
                :disabled="item.id === isLoadingItem"
              >
                查看更多
              </button>
              <button
                type="button"
                class="btn btn-outline-danger"
                @click="addToCart(item.id)"
                :disabled="item.id === isLoadingItem"
              >
                <span
                  v-show="item.id === isLoadingItem"
                  class="spinner-grow spinner-grow-sm"
                ></span>
                加到購物車
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import emitter from '@/libs/emitter';

const apiUrl = process.env.VUE_APP_API;
const apiPath = process.env.VUE_APP_PATH;

export default {
  data() {
    return {
      // 購物車列表
      cartData: {
        carts: [],
      },
      // 產品列表
      products: [],
      isLoadingItem: '',
    };
  },
  methods: {
    // 取得產品列表
    getProducts() {
      this.$http
        .get(`${apiUrl}api/${apiPath}/products/all`)
        .then((res) => {
          this.products = res.data.products;
        })
        .catch((err) => {
          alert(err.data.message);
        });
    },
    // 加入購物車
    addToCart(id, qty = 1) {
      const data = {
        product_id: id,
        qty,
      };
      this.isLoadingItem = id;
      this.$http
        .post(`${apiUrl}api/${apiPath}/cart`, { data })
        .then((res) => {
          alert(res.data.message);
          this.isLoadingItem = '';
          emitter.emit('get-cart');
        })
        .catch((err) => {
          alert(err.data.message);
        });
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>
